import React, { Component } from 'react';
import { Footer, PatternBg, TXCStatus } from './Utils.js';
import Web3 from 'web3';
import ReactTooltip from 'react-tooltip';
import axios from 'axios';

import logo from './img/SVG/logo.svg';
import './App.css';

import TXQueueContainer from './TXQueueContainer.js';
import SchedulerContainer from './SchedulerContainer.js';
import TXCContainer from './TXCContainer.js';
import NoticesContainer from './NoticesContainer.js';
import DepositContainer from './DepositContainer.js';
import BalanceContainer from './BalanceContainer.js';
import SendRawContainer from './SendRawContainer.js';


class App extends Component {
  constructor(props){
    super();
    
    this.state = {
      addressList : [],
      queue : {},
      blockNumber : 0,
      balance: 0,
      netID: 1,
      hasTXC: false,
      blocksFormat: true,
      server: 'prod',
    }
    
    this.updateBlock = this.updateBlock.bind(this);
    this.web3 = new Web3(Web3.givenProvider || "http://localhost:8545");

    this.addresses = {
      [42]: {
        eths: "0xa0475d202be30019e56306cc08f8998887135258",
        eths_old: "0xe49bb13f1e57de8364586bbb82bf79d0c8aac074",
        txcsale: "0x2ffbee1b7eda9d6187f4f875899c9f8f28d927ed",
        txc: "0x12C942fDbE9981E68DC153CC92dA2e2c301F5a9A",
      },
      [1]: {
        eths: "0xf99E87098f4F1C8d8bd6a3df7181B3acdc21848A",
        txcsale: "0xa4b01a89162141c58505ea43db2d3bda71a325c2",
        txc: "0x67e35c41060a988f59e2bcb2e0f09b6978fb6614",
      }
    };

    this.urls = {
      'dev': "http://localhost:5000",
      'prod': "https://api.txcast.io",
    }

    this.abiTypes = {
      eths: {
        withoutSignature: [
          'address', // target
          'uint256', // amount
          'uint256', // reward
          'bool',    // byBlock
          'uint256', // condition
          'uint256', // nonce
          'uint256', // netID
        ],
        withSignature: [
          'address', // target
          'uint256', // amount
          'uint256', // reward
          'bool',    // byBlock
          'uint256', // condition
          'uint256', // nonce
          'uint256', // netID
          'bytes',   // signature 
        ],
      }
    }

  }

  async componentWillMount() {
    await window.ethereum.enable();

    const a = await this.web3.eth.getAccounts();
    const id = await this.web3.eth.net.getId();

    await this.setState({addressList: a, netID: id});

    const address = a[0];
    const callData = {
      to: this.addresses[this.state.netID].txc,
      data:'0x70a08231' + this.web3.eth.abi.encodeParameters(
        [
          'address',
        ],
        [address]
      ).substr(2)
    }

    console.log(callData)

    this.web3.eth.call(callData).then((b,s) => {
      this.setState({hasTXC: parseInt(b) > 0});
    });

    setInterval(this.updateBlock, 5000);
  }

  fetchData() {
    console.log(this.state)
    const req = {
      url: this.urls[this.state.server] + '/signature',
      method: 'GET',
    };

    axios(req).then(ans => {
      const queue = ans.data.reduce((acc, v) => { acc[v[0]] = v[1]; return acc; }, {});
      this.setState({queue: queue});
    });

  }

  async updateBlock() {
    const latestBlock = await this.web3.eth.getBlock('latest');
    if(latestBlock !== null && /[0-9]*/.test(latestBlock)) this.setState({blockNumber: latestBlock.number});
  }

  async onSign(schedulerInput) {
    let latestBlock = await this.web3.eth.getBlock('latest');
    const dataArray = [
      schedulerInput.address,
      this.web3.utils.toWei(schedulerInput.amount.toString(),'ether'),
      this.web3.utils.toWei(schedulerInput.reward.toString(),'ether'),
      true,
      parseInt(schedulerInput.blocks) + parseInt(latestBlock.number),
      Math.floor(Math.random() * Number.MAX_SAFE_INTEGER),
      this.state.netID,
    ];

    console.log(dataArray);

    const callData = {
      to: this.addresses[this.state.netID].eths,
      data:'0x889c3912' + this.web3.eth.abi.encodeParameters(this.abiTypes.eths.withoutSignature, dataArray).substr(2),
    }

    this.web3.eth.call(callData).then(hash => {
      console.log(hash);
      console.log(this.web3.utils.hexToAscii(hash));

      const signer = this.state.addressList[0];
      this.web3.eth.personal.sign(hash, signer)
        .then(signature => {
          console.log([...dataArray, signature].toString())

          const callDataTX = {
            to: this.addresses[this.state.netID].eths,
            data:'0x392482d1' + this.web3.eth.abi.encodeParameters(this.abiTypes.eths.withSignature, [...dataArray, signature]).substr(2),
          }

          const txrequest = {
            currency: 'ETH',
            amount: this.web3.utils.fromWei(dataArray[2].toString(), 'ether'),
            blocks: dataArray[4],
            address: signer, 
            dataArray: JSON.stringify(dataArray),
            callDataTX: JSON.stringify(callDataTX),
          }

          const appended = this.state.queue;
          appended[signature] = txrequest;

          this.setState({
            queue: appended,
          });

          console.log(txrequest)

          const formData = new FormData();
          Object.keys(txrequest).forEach(k => {
            formData.set(k,txrequest[k]);
          });

          const req = {
            url: this.urls[this.state.server] + '/signature/' + signature,
            data: formData,
            method: 'PUT',
          };

          axios(req).then(console.log).catch(console.log)
        });
    });
  }

  onBuy() {
    const address = this.state.addressList[0];
    const amount = this.web3.utils.toWei("1",'ether');

    const callDataTX = {
      from: address,
      value: amount, 
      to: this.addresses[this.state.netID].txcsale,
      data:'0xec8ac4d80' + this.web3.eth.abi.encodeParameters(
        [
          'address',
        ],
        [address],
      ).substr(2)
    }

    this.sendTX(callDataTX);
  }

  onCancel(signature, nonce) {
    const callDataTX = {
      to: this.addresses[this.state.netID].eths,
      data:'0x8e7baff6' + this.web3.eth.abi.encodeParameters(
        [
          'uint',
        ],
        [ nonce ],
      ).substr(2)
    }

    this.sendTX(callDataTX).then(receipt => {
      this.web3.eth.personal.sign("Cancel TX " + nonce, this.state.addressList[0]).then(verified => {
        const cancelOrder = {
          signature: signature,
          nonce: nonce, 
          verified: verified,
        }

        console.log("cancel",cancelOrder)

        const formData = new FormData();
        Object.keys(cancelOrder).forEach(k => {
          formData.set(k, cancelOrder[k]);
        });

        const req = {
          url: this.urls[this.state.server] + '/signature/' + signature,
          data: formData,
          method: 'DELETE',
        };

        axios(req)
          .then(this.fetchData.bind(this))
          .catch(console.error)
      });
    });
  }

  onDeposit(depositInput) {
    const address = this.state.addressList[0];
    const amount = this.web3.utils.toWei(depositInput.amount.toString(),'ether');

    const callDataTX = {
      from: address,
      value: amount, 
      to: this.addresses[this.state.netID].eths,
      data:'0xf340fa01' + this.web3.eth.abi.encodeParameters(
        [
          'address',
        ],
        [address],
      ).substr(2)
    }

    this.sendTX(callDataTX).then(console.log);
  }

  onBalance(balanceInput) {
    const callData = {
      to: this.addresses[this.state.netID].eths,
      data:'0x70a08231' + this.web3.eth.abi.encodeParameters(
        [
          'address',
        ],
        [balanceInput.address]
      ).substr(2)
    }

    console.log(callData)

    this.web3.eth.call(callData).then((b,s) => {
      const balance = this.web3.utils.fromWei(this.web3.utils.toBN(b).toString(), 'ether');
      this.setState({balance: balance})
    });
  }

  onSendRaw(rawInput) {
    console.log(rawInput)

    this.sendTX(rawInput.callDataTX);
  }

  sendTX(callDataTX) {
    const address = this.state.addressList[0];
    const callDataTXFrom = {...callDataTX, from: address}

    console.log(callDataTXFrom)

    return this.web3.eth.sendTransaction(callDataTXFrom);
  }

  render() {
    return (
      <div>
        <PatternBg />
        <ReactTooltip />
        <nav className="navbar navbar-light justify-content-between bg-white mb-3">
          <a className="navbar-brand text-monospace font-weight-bold" href="#" style={{"letterSpacing":"7px"}}>
            <img src={logo} width="30" height="30" className="d-inline-block align-top mx-3" alt="txcast logo"/>
            TXCast 
          </a>
          <span className="navbar-text ml-auto">
            <i style={{"color": this.state.netID === 1 ? "teal": "purple"}} className="fas fa-wifi mr-2"></i>
            Active TXCast Address: <span className="text-monospace">{this.addresses[this.state.netID].eths}</span>
          </span>
          <TXCStatus active={this.state.hasTXC ? true : false} />
          <span className="navbar-text ml-3">
            Current block: <span className="text-monospace">{this.state.blockNumber}</span>
          </span>
        </nav>
        <div className="container-fluid px-4 mt-4" style={{'width':'100%'}}>
          <div className="row">
            <div
              className="col-sm-5"
              style={{'minWidth':'500px'}}
            >
              <TXQueueContainer
                account={this.state.addressList[0]}
                queue={this.state.queue}
                blockNumber={this.state.blockNumber}
                sendTX={this.sendTX.bind(this)}
                onCancel={this.onCancel.bind(this)}
                fetchData={this.fetchData.bind(this)}
              />
              <SendRawContainer 
                onSendRaw={this.onSendRaw.bind(this)}
              />
            </div>
            <div className="col-sm-7">
              <div className="row">
                <div
                  className="col-lg-7 col-12"
                  style={{'minWidth':'380px'}}
                >
                  <SchedulerContainer
                    onSign={this.onSign.bind(this)}
                  />
                  <DepositContainer
                    address={this.state.addressList[0]}
                    onDeposit={this.onDeposit.bind(this)}
                  />
                </div>
                <div
                  className="col-lg-5 col-12"
                  style={{'minWidth':'100px'}}
                >
                  <TXCContainer
                    onBuy={this.onBuy.bind(this)}
                  />
                  <BalanceContainer
                    address={this.state.addressList[0]}
                    balance={this.state.balance}
                    onBalance={this.onBalance.bind(this)}
                  />
                  <NoticesContainer/>
                </div>
              </div>
            </div>
          </div>
        </div>
        <Footer />
      </div>
    );
  }
}

export default App;
